
.equipment-editor {
  width: 100%;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  background: var(--input-bg);
  overflow: hidden;

  &.is-disabled {
    opacity: 0.6;
    pointer-events: none;
  }
}

.equipment-list {
  max-height: 400px;
  overflow-y: auto;
  padding: var(--spacing-sm);

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: var(--border-color-light);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--border-color);
    border-radius: 3px;

    &:hover {
      background: var(--border-color-strong);
    }
  }
}

.equipment-item {
  background: var(--card-bg);
  border: 1px solid var(--border-color-light);
  border-radius: var(--radius-md);
  padding: var(--spacing-md);
  margin-bottom: var(--spacing-sm);
  transition: all var(--transition-fast);

  &:last-child {
    margin-bottom: 0;
  }

  &:hover:not(.editing) {
    border-color: var(--accent-color);
    box-shadow: var(--shadow-sm);
  }

  &.editing {
    border-color: var(--accent-color);
    background: rgba(212, 175, 55, 0.05);
  }
}

.item-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-xs);
}

.item-type {
  padding: 2px 8px;
  background: var(--border-color-light);
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  color: var(--text-light);
}

.item-name {
  flex: 1;
  font-size: 1rem;
  font-weight: 600;
  color: var(--title-color);
}

.item-rarity {
  padding: 2px 10px;
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  font-weight: 600;
  background: var(--rarity-color);
  color: #fff;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}

.item-effect {
  font-size: 0.9rem;
  color: var(--text-color);
  margin-bottom: var(--spacing-xs);

  .effect-label {
    color: var(--accent-color);
    font-weight: 600;
  }
}

.item-tag {
  margin-bottom: var(--spacing-xs);

  .tag-text {
    display: inline-block;
    padding: 2px 8px;
    background: rgba(212, 175, 55, 0.15);
    border: 1px solid rgba(212, 175, 55, 0.3);
    border-radius: var(--radius-sm);
    font-size: 0.8rem;
    color: var(--accent-color);
  }
}

.item-description {
  font-size: 0.85rem;
  color: var(--text-light);
  font-style: italic;
  padding-top: var(--spacing-xs);
  border-top: 1px dashed var(--border-color-light);
}

.item-actions {
  display: flex;
  gap: var(--spacing-sm);
  margin-top: var(--spacing-sm);
  padding-top: var(--spacing-sm);
  border-top: 1px solid var(--border-color-light);
}

.action-btn {
  padding: var(--spacing-xs) var(--spacing-md);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-sm);
  background: var(--card-bg);
  font-size: 0.8rem;
  cursor: pointer;
  transition: all var(--transition-fast);

  &:hover:not(:disabled) {
    border-color: var(--accent-color);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &.delete:hover:not(:disabled) {
    border-color: var(--error-color);
    color: var(--error-color);
    background: rgba(211, 47, 47, 0.05);
  }
}

.edit-form,
.add-form {
  padding: var(--spacing-md);
}

.add-form {
  border-top: 1px solid var(--border-color-light);
  background: rgba(212, 175, 55, 0.03);
}

.add-form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-md);

  .form-title {
    font-weight: 600;
    color: var(--title-color);
  }

  .close-btn {
    width: 24px;
    height: 24px;
    padding: 0;
    border: none;
    background: none;
    cursor: pointer;
    font-size: 0.9rem;
    color: var(--text-light);
    transition: color var(--transition-fast);

    &:hover {
      color: var(--error-color);
    }
  }
}

.form-row {
  margin-bottom: var(--spacing-sm);

  &.half {
    flex: 1;
  }
}

.form-row-group {
  display: flex;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-sm);
}

.edit-actions,
.add-form-actions {
  display: flex;
  gap: var(--spacing-sm);
  margin-top: var(--spacing-md);
  justify-content: flex-end;
}

.btn-cancel,
.btn-save,
.btn-add {
  padding: var(--spacing-sm) var(--spacing-lg);
  border: none;
  border-radius: var(--radius-md);
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.btn-cancel {
  background: var(--border-color-light);
  color: var(--text-color);

  &:hover {
    background: var(--border-color);
  }
}

.btn-save,
.btn-add {
  background: var(--accent-color);
  color: var(--primary-bg);

  &:hover:not(:disabled) {
    background: #c9a842;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-xl);
  color: var(--text-light);
  gap: var(--spacing-sm);

  .empty-icon {
    font-size: 2rem;
    opacity: 0.6;
  }

  .empty-text {
    font-size: 0.9rem;
  }
}

.add-section {
  padding: var(--spacing-sm);
  border-top: 1px solid var(--border-color-light);
}

.add-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-sm);
  width: 100%;
  padding: var(--spacing-md);
  background: rgba(212, 175, 55, 0.1);
  border: 2px dashed var(--accent-color);
  border-radius: var(--radius-md);
  color: var(--accent-color);
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-fast);

  &:hover:not(:disabled) {
    background: rgba(212, 175, 55, 0.2);
    border-style: solid;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  .add-icon {
    font-size: 1.2rem;
    font-weight: 700;
  }
}

.count-info {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: var(--spacing-xs) var(--spacing-sm);
  background: var(--border-color-light);
  font-size: 0.75rem;
  color: var(--text-light);
  gap: 2px;

  .count-current {
    font-weight: 700;
    color: var(--accent-color);
  }

  .count-max {
    font-weight: 600;
  }
}

// 列表动画
.equip-list-enter-active,
.equip-list-leave-active {
  transition: all 0.3s ease;
}

.equip-list-enter-from {
  opacity: 0;
  transform: translateY(-10px);
}

.equip-list-leave-to {
  opacity: 0;
  transform: translateX(20px);
}

.equip-list-move {
  transition: transform 0.3s ease;
}

// 响应式设计
@media (max-width: 768px) {
  .form-row-group {
    flex-direction: column;
    gap: var(--spacing-sm);
  }

  .item-header {
    flex-wrap: wrap;
  }

  .item-actions {
    flex-wrap: wrap;
  }
}
