
.form-array-input {
  width: 100%;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  background: var(--input-bg);
  overflow: hidden;

  &.is-disabled {
    opacity: 0.6;
    pointer-events: none;
  }
}

.array-items {
  max-height: 200px;
  overflow-y: auto;
  padding: var(--spacing-sm);

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: var(--border-color-light);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--border-color);
    border-radius: 3px;

    &:hover {
      background: var(--border-color-strong);
    }
  }
}

.array-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm) var(--spacing-md);
  background: var(--card-bg);
  border: 1px solid var(--border-color-light);
  border-radius: var(--radius-md);
  margin-bottom: var(--spacing-xs);
  transition: all var(--transition-fast);

  &:last-child {
    margin-bottom: 0;
  }

  &:hover {
    border-color: var(--accent-color);
    box-shadow: var(--shadow-sm);
  }
}

.item-index {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  background: var(--accent-color);
  color: var(--primary-bg);
  border-radius: 50%;
  font-size: 0.75rem;
  font-weight: 700;
  flex-shrink: 0;
}

.item-text {
  flex: 1;
  font-size: 0.95rem;
  color: var(--text-color);
  cursor: pointer;
  padding: var(--spacing-xs) 0;
  min-height: 28px;
  display: flex;
  align-items: center;

  &:hover {
    color: var(--accent-color);
  }
}

.item-input {
  flex: 1;
  padding: var(--spacing-xs) var(--spacing-sm);
  background: #fff;
  border: 1px solid var(--accent-color);
  border-radius: var(--radius-sm);
  font-size: 0.95rem;
  color: var(--text-color);
  outline: none;

  &:focus {
    box-shadow: 0 0 0 2px rgba(212, 175, 55, 0.2);
  }
}

.item-actions {
  display: flex;
  gap: 4px;
  opacity: 0;
  transition: opacity var(--transition-fast);

  .array-item:hover & {
    opacity: 1;
  }
}

.action-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 26px;
  height: 26px;
  padding: 0;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-sm);
  background: var(--card-bg);
  cursor: pointer;
  font-size: 0.75rem;
  transition: all var(--transition-fast);

  &:hover:not(:disabled) {
    border-color: var(--accent-color);
    background: rgba(212, 175, 55, 0.1);
  }

  &:disabled {
    opacity: 0.4;
    cursor: not-allowed;
  }

  &.remove-btn:hover:not(:disabled) {
    border-color: var(--error-color);
    background: rgba(211, 47, 47, 0.1);
    color: var(--error-color);
  }
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-lg);
  color: var(--text-light);
  gap: var(--spacing-sm);

  .empty-icon {
    font-size: 1.5rem;
    opacity: 0.6;
  }

  .empty-text {
    font-size: 0.85rem;
  }
}

.add-item-section {
  display: flex;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm);
  border-top: 1px solid var(--border-color-light);
  background: rgba(212, 175, 55, 0.03);
}

.add-input {
  flex: 1;
  padding: var(--spacing-sm) var(--spacing-md);
  background: #fff;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  font-size: 0.9rem;
  color: var(--text-color);
  outline: none;
  transition: all var(--transition-fast);

  &::placeholder {
    color: var(--text-light);
  }

  &:focus {
    border-color: var(--accent-color);
    box-shadow: 0 0 0 2px rgba(212, 175, 55, 0.1);
  }

  &:disabled {
    background: var(--border-color-light);
    cursor: not-allowed;
  }
}

.add-btn {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  padding: var(--spacing-sm) var(--spacing-md);
  background: var(--accent-color);
  color: var(--primary-bg);
  border: none;
  border-radius: var(--radius-md);
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-fast);
  white-space: nowrap;

  &:hover:not(:disabled) {
    background: #c9a842;
    transform: translateY(-1px);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
  }

  .add-icon {
    font-size: 1rem;
    font-weight: 700;
  }
}

.count-info {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: var(--spacing-xs) var(--spacing-sm);
  background: var(--border-color-light);
  font-size: 0.75rem;
  color: var(--text-light);
  gap: 2px;

  .count-current {
    font-weight: 700;
    color: var(--accent-color);
  }

  .count-max {
    font-weight: 600;
  }
}

// 列表动画
.list-enter-active,
.list-leave-active {
  transition: all 0.3s ease;
}

.list-enter-from {
  opacity: 0;
  transform: translateX(-20px);
}

.list-leave-to {
  opacity: 0;
  transform: translateX(20px);
}

.list-move {
  transition: transform 0.3s ease;
}

// 响应式设计
@media (max-width: 768px) {
  .add-item-section {
    flex-direction: column;
  }

  .add-btn {
    justify-content: center;
  }

  .item-actions {
    opacity: 1;
  }

  .action-btn {
    width: 28px;
    height: 28px;
  }
}
