
.requirement-item {
  display: flex;
  flex-direction: column;
  gap: 2px;
  font-size: 0.75rem;

  .requirement-label {
    color: var(--text-light);
    font-weight: 500;
    font-size: 0.85rem;
  }

  .requirement-value {
    font-weight: 600;
    word-break: break-word;
    line-height: 1.4;

    &.requirement-met {
      color: var(--success-color);
    }

    &.requirement-unmet {
      color: var(--error-color);
    }
  }
}
